import React from 'react'
import {
	Button,
	WhiteSpace,
	WingBlank
} from 'antd-mobile'

class Errors extends React.Component{
	constructor(props) {
		super(props)
		this.state = {
			counter: 3
		}
	}
	componentDidMount() {
		let timer = setInterval(() => {
			if (this.state.counter <= 1) {
				clearInterval(timer)
				this.props.history.goBack()
				return
			}
			this.setState({
				counter: this.state.counter - 1
			})
		}, 1000)
	}
	componentWillUnmount() {
	}
	backHandle() {
		this.props.history.goBack()
	}
	render() {
		return (
			<WingBlank>
				<WhiteSpace/>
				<h2>当前页面没有找到</h2>
          		<p>{this.state.counter}秒后返回上一页</p>
				<Button onClick={this.backHandle.bind(this)}>返回</Button>
			</WingBlank>
		)
	}
}

export default Errors